.calc {
  $max-width: 830px;

  &__tab_end {
    max-width: $max-width;
    margin: 0 auto;

    &-form {
      padding: 30px 0 0 0;
      @include media(xl) {
        padding: 0;
      }

      &-row {
        $gutter: 20px;
        $gutter-xl: 15px;

        position: relative;
        margin: 0 -#{$gutter};
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        @include media(xl) {
          margin: 0 -#{$gutter-xl};
        }

        &-col {
          padding: 0 $gutter;
          display: flex;
          flex-direction: column;
          width: 50%;
          margin: 0 0 $gutter 0;
          align-items: flex-end;
          margin-bottom: 30px;
          @include media(xl) {
            padding: 0 $gutter-xl;
          }
          @include media(xxs) {
            width: 100%;
          }

          &-select {
            width: 100%;
            @include media_min(xxs) {
              max-width: 300px;
              margin: 0 auto;
            }
          }

        }

      }

      &-max_width {
        max-width: 740px;
        margin: 0 auto;

        label {
          text-align: left;
          font-size: 18px;
          @include media(xxs) {
            font-size: 16px;
          }
        }

        .form-control {
          text-align: left;
        }

      }

      &-description {
        color: $color-blue;
        font-size: 12px;
        line-height: 1.3;
        text-align: center;
        margin: 0 0 25px 0;
      }

      &-submit {
        text-align: center;
        padding: 0 0 25px 0;
        @include media(lg) {
          padding: 0;
        }

        .btn {
          width: 250px;
        }
      }

    }

    .calc__tabs {

      &-content {

        &-title {
          text-transform: uppercase;
          color: $color-orange;
          font-weight: 500;
          letter-spacing: .08em;
          font-size: 34px;
          @include media(lg) {
            font-size: 24px;
          }

        }

        &-excerpt {
          font-weight: 500;
          color: $color-blue;
          font-size: 26px;
          @include media(lg) {
            font-size: 18px;
          }
        }
      }
    }

  }
}
